 la infancia, el cerebro responde con mayor flexibilidad a los estímulos, lo que permite la construcción de bases sólidas para el desarrollo cognitivo, social y emocional. En cambio, en la adultez, aunque el cerebro sigue siendo adaptable, la formación de nuevas conexiones neuronales requiere un mayor esfuerzo y práctica.")]),_c('p',{attrs:{"numero":"4","titulo":"Desde la psicología"}},[_vm._v("Desde la psicología, Babativa (2017), asegura que se ha evidenciado que el desarrollo infantil no solo depende de la estimulación externa, sino también de la interacción afectiva y del contexto en el que crece el niño. El apego seguro, la exploración del entorno y la interacción con otros niños y adultos, potencian el desarrollo de habilidades sociales y emocionales. De este modo, la combinación entre factores neurobiológicos y experiencias ambientales, moldea el proceso de aprendizaje y adaptación.")])])],1)]),_vm._m(10),_c('p',{staticClass:"mb-4"},[_vm._v("En este sentido, comprender cómo se construye el conocimiento en la infancia, resulta fundamental.")]),_vm._m(11),_vm._m(12),_vm._m(13),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/15.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('h5',[_vm._v("Sensoriomotora (0-2 años)")]),_c('p',[_vm._v("El aprendizaje ocurre a través de los sentidos y el movimiento. En esta fase, el niño descubre la permanencia del objeto, comprendiendo que los elementos existen incluso cuando no están a la vista.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/16.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg