orno enriquecido con materiales adecuados, espacios flexibles y oportunidades de exploración, favorece experiencias lúdicas que potencian el aprendizaje.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Entonces:")]),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/41.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("¿Cómo se pueden integrar el juego y la enseñanza de manera efectiva?")]),_c('p',[_vm._v("La clave está en permitir que el aprendizaje suceda de forma natural, aprovechando el interés innato de los niños por descubrir, experimentar y jugar. Cuando el juego se convierte en el eje de las experiencias educativas, el aprendizaje deja de ser una obligación y se transforma en una aventura llena de significado y disfrute. A través de esta metodología, no solo se fomenta el desarrollo de habilidades cognitivas, sociales y emocionales, sino que también se fortalece la motivación y la autonomía del niño en su proceso de aprendizaje.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/42.png")}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("¿Qué tan determinante es el rol de la familia y los profesionales en el desarrollo infantil?")]),_c('p',[_vm._v("La estimulación temprana no ocurre de manera aislada, sino que es el resultado de una interacción constante entre el niño y su entorno. En este proceso, la familia y los profesionales de la educación y la salud, juegan un papel clave, pues son quienes crean las condiciones necesarias para potenciar el desarrollo en sus diferentes dimensiones. La calidad de las interacciones, la disponibilidad de materiales adecuados y el acompañamiento afectivo, son aspectos fundamentales que contribuyen a un crecimiento saludable y equilibrado.")])])])])],1)]),_c('p',{staticClass:"mb-4"},[_vm._v("Así, cuando familia y profesionales trabajan de manera articulada, se generan oportunidades de aprendizaje enriquecedoras que benefician significativamente el bienestar infantil:")]),_c('div',{staticClass:"row align-items-center mb-5"},[_vm._m(34),_c('div',{staticClass:"col-lg-8 order-1 mb-3 mb-lg-0"},[_c('p',{attrs:{"data-aos":"fade-left"}}),_c('LineaTiempoD',{staticClass:"color-primario large",attrs:{"data-aos":"fade-left"}},[_c('p',{attrs:{"numero":"1","titulo":"La familia"}},[_vm._v("La familia, como primer entorno de aprendizaje, proporciona las experiencias afectivas y cognitivas, fundamentales en los primeros años de vida. Cada interacción cotidiana, desde una conversación hasta un juego compartido, contribuye a fortalecer el desarrollo lingüístico, social y emocional del niño. Asimismo, el vínculo seguro con los cuidadores, fomenta la confianza y la exploración, elementos esenciales para un aprendizaje significativo. ")]),_c('p',{attrs:{"numero":"2","titulo":"Los profesionales de la educación y la salud"}},[_vm._v("Los profesionales de la educación y la salud, tienen la responsabilidad de orientar, diseñar y ejecutar estrategias de estimulación, acordes con las necesidades individuales de cada niño. A través de un enfoque interdisciplinario, pueden identificar señales de alerta, intervenir oportunamente y guiar a las familias en la creación de ambientes enriquecidos que favorezcan el desarrollo infantil. Además, su labor permite garantizar que la estimulación se fundamente en principios científicos, asegurando su efectividad y pertinencia.")]),_c('p',{attrs:{"numero":"3","titulo":"Trabajan en conjunto"}},[_vm._v("Cuando la familia y los profesionales trabajan en conjunto, se genera una sinergia que maximiza las oportunidades de aprendizaje y bienestar para el niño. Por ello